import {
  getAssetFromKV,
  serveSinglePageApp,
} from "@cloudflare/kv-asset-handler";
import { Leaderboard } from "./leaderboard.js";

export { Leaderboard };

export default {
  async fetch(request, env, ctx) {
    const url = new URL(request.url);

    // /api/leaderboard çağrılarını Durable Object'a yönlendir
    if (url.pathname.startsWith("/api/leaderboard")) {
      const id = env.LEADERBOARD.idFromName("GLOBAL");
      const stub = env.LEADERBOARD.get(id);
      return stub.fetch(request);
    }

    // Diğer tüm istekleri public/ altından servis et
    // __STATIC_CONTENT binding'ini kullanarak statik dosyaları getir
    try {
      // Kök dizin isteği (/) gelirse index.html'i servis etmek için serveSinglePageApp kullan
      if (url.pathname === "/" || url.pathname.endsWith(".html")) {
        return await getAssetFromKV(request, { env, serveSinglePageApp: true });
      }
      // Diğer statik dosyalar için
      return await getAssetFromKV(request, { env });
    } catch (e) {
      // Eğer statik dosya bulunamazsa 404 döndür
      return new Response(e.message || e.toString(), { status: 404 });
    }
  },
};
